@import "../../node_modules/filepond/dist/filepond.min.css";

@import "../../node_modules/filepond-plugin-image-preview/dist/filepond-plugin-image-preview.min.css";

.screen1-head__title {
  font-size: 2.6rem;
  line-height: 2.9rem;
}

.screen1-foot {
  padding: 1.1rem 0 7rem;
}

.screen1-foot p {
  font-size: 20px;
  line-height: 30px;
}

.screen1-foot__text {
  padding: 0;
  width: 50%;
  margin-left: 10px;
}

.icon-arrow-right {
  fill: #46bcec;
}

.offers {
  position: relative;
  bottom: 25px;
  padding-bottom: 7.5rem;
}

.offers-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row wrap;
}

.offer1,
.offer2 {
  box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  width: 335px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row wrap;
}

.offer-text {
  font-weight: 900;
  font-size: 1.1rem;
  line-height: 1.4rem;
  text-align: center;
  padding: 0 21px;
}

.offer1 {
  background: url(../img/Map.jpg) no-repeat center center;
  margin-bottom: 20px;
}

.offer2 {
  background: #ffffff;
}

.offer1 .offer-text {
  padding-top: 60px;
  padding-bottom: 57px;
}

.offer2 .offer-text {
  padding-top: 5px;
  padding-bottom: 52px;
}

.icon-camera {
  padding-top: 38px;
}

.card-modal {
  position: relative;
  bottom: 55px;
  background: #8bc34a;
  align-items: center;
  display: none;
  flex-flow: column wrap;
  padding: 1.5rem 1.25rem 1.25rem;
  margin: 0 -1.25rem;
}

.card-modal--yellow {
  background: #fdd835 !important;
}

.card-modal--blue {
  background: #46bcec !important;
}

.card-modal--white {
  background: #fff !important;
}

.card-modal--rel {
  margin: 0;
  bottom: 0;
}

.card-modal--pos-right {
  margin: 0 -1.25rem;
}

.card-modal__inner {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-flow: column wrap;
  min-width: 280px;
}

.card-modal__title {
  font-weight: 900;
  font-size: 1rem;
  line-height: 1.4rem;
  padding-bottom: 17px;
  width: 100%;
  text-align: center;
}

.card-modal__date {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 335px;
  width: 100%;
  margin: 5px 0;
}

.card-modal__date-right,
.card-modal__date-left {
  flex: 1;
  border: 2px solid #fff;
  padding: 14px 10px;
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 1rem;
  background: #8bc34a;
}

.card-modal__date-right {
  border-radius: 0 15px 15px 0;
  border-left: 0;
}

.card-modal__date-left {
  border-radius: 15px 0 0 15px;
}

.card-modal__item {
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 1rem;
  border-radius: 15px;
  border: 2px solid #fff;
  background: #8bc34a;
  margin: 5px 0;
  padding: 0.9rem 0;
  width: 100%;
  max-width: 335px;
}

.card-modal__item--yellow {
  background: #fdd835 !important;
}

.card-modal__item--white {
  background: #fff !important;
  border: 2px solid #f2f2f2 !important;
}

.card-modal__item--dark {
  background: #000 !important;
  color: #fff !important;
}

.card-modal__item--blue {
  background: #46bcec !important;
}

.card-modal__button {
  font-weight: 900;
  font-size: 1.1rem;
  line-height: 1.3rem;
  background: #fff;
  color: #000;
  order: 1000;
}

.card-modal__button--mtop {
  margin: 2.8rem 0;
}

.card-modal__close {
  position: relative;
  width: 15px;
  height: 24px;
  margin-top: 15px;
}

.card-modal__close span {
  position: absolute;
  height: 4px;
  width: 24px;
  background: #000;
  transform: rotate(45deg);
  right: 0;
}

.card-modal__close::before {
  position: absolute;
  content: "";
  height: 4px;
  width: 24px;
  background: #000;
  transform: rotate(-45deg);
  right: 0;
}

.modal-upload {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row wrap;
  order: 999;
  width: 100%;
}

.modal-upload__text {
  margin-left: 10px;
  font-weight: 900;
  font-size: 1rem;
  line-height: 1.2rem;
}

.footer-list__item a {
  color: #46bcec;
}

.icon-envelope {
  fill: #46bcec;
}

.filepond {
  /*Верхняя надпись*/
  /*Кнопка Browse*/
  /*Основные кнопки*/
  /*Обертка всей библиотеки*/
  /*Имя файла*/
  /*Начало файлов*/
  /*Цвета статусов у info*/
}

.filepond--root {
  width: 100%;
  font-family: "Roboto", sans-serif;
  margin-bottom: 1rem;
}

.filepond--item {
  width: calc(50% - 0.5em);
}

.filepond--item-panel {
  background-color: transparent;
}

.filepond--drop-label {
  color: #000000;
  width: 100%;
  margin: 0 auto;
  cursor: pointer;
  transition: 1s;
}

.filepond--label-action {
  text-decoration: none;
  margin-left: 10px;
  font-weight: 900;
  line-height: 19px;
}

.filepond--file-action-button {
  cursor: pointer;
  background: none;
}

.filepond--file-action-button:hover,
.filepond--file-action-button:focus {
  box-shadow: 0 0 0 0.125em rgba(0, 0, 0, 0.5);
}

.filepond--panel-root {
  border-radius: 15px;
  background-color: transparent;
}

.filepond--file-info {
  display: none !important;
}

.filepond--list-scroller {
  transition: 1s;
}

.filepond--image-preview-overlay-idle,
.filepond--image-preview-overlay-success,
.filepond--image-preview-overlay-failure {
  color: transparent;
}

.filepond--drop-label.filepond--drop-label label {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  cursor: pointer;
}

@media (min-width: 768px) {
  .screen1-foot__text {
    width: 45%;
  }

  .offer1,
  .offer2:not(:last-child) {
    margin-right: 40px;
  }

  .offer1 {
    margin-bottom: 0;
  }

  .card-modal {
    position: fixed;
    margin: 0;
    z-index: 1;
    border-radius: 15px;
    box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.15);
  }

  .card-modal__inner {
    min-width: 335px;
  }

  .card-modal--rel {
    position: relative;
    border-radius: 0;
  }

  .card-modal__title {
    flex: 100%;
    text-align: center;
  }

  .modal-upload__text {
    margin-right: auto;
  }

  .icon-images {
    margin-left: auto;
  }

  .filepond--item {
    width: calc(33% - 0.5em);
  }
}

@media (min-width: 768px) and (orientation: landscape) {
  .card-modal--transform {
    transform: scale(0.8);
    bottom: -20px;
  }
}

@media (min-width: 1200px) {
  .card-modal--pos-right {
    margin: 0 -80px;
  }
}

@media (min-width: 1200px) and (orientation: landscape) {
  .card-modal--transform {
    transform: scale(1);
    bottom: 55px;
  }
}

@media (min-width: 1440px) {
  .header-list__item a:hover {
    color: #46bcec;
  }

  .screen1-head {
    max-width: 1250px;
  }

  .screen1-head__text {
    margin-left: 35px;
    max-width: 550px;
    width: 100%;
  }

  .screen1-foot {
    display: flex;
    padding: 1.75rem 0 3rem;
  }

  .screen1-foot p {
    font-size: 32px;
    line-height: 48px;
  }

  .card-modal--pos-right {
    box-shadow: none;
  }

  .card-modal--rel {
    flex-flow: row wrap;
    justify-content: center;
    padding: 3.1rem 1.7rem 4rem;
    margin: 0;
  }

  .card-modal__inner {
    max-width: 1280px;
    width: 100%;
  }

  .card-modal__inner--flex-row {
    flex-flow: row wrap;
  }

  .card-modal__title {
    font-size: 1.5rem;
    line-height: 29px;
  }

  .card-modal__item {
    flex: 1;
  }

  .card-modal__item--mr40:not(:last-child) {
    margin-right: 40px;
  }

  .card-modal__button--mtop {
    margin: 2.8rem 0 0;
  }

  .modal-upload {
    order: 0;
  }

  .filepond--root {
    margin-bottom: -1rem;
  }

  .filepond--item {
    width: calc(10% - 1.6em);
  }

  .filepond--drop-label {
    width: 20%;
  }
}